import {Accordion, AccordionSummary,AccordionDetails,Typography, Button} from '@material-ui/core'
import ExpandMoreIcon from "@material-ui/icons/ExpandMore"
import * as C from '../util/constants'
import {useState} from 'react'

interface worlds_props{
    setWorld: (g: string)=> void
    name: string
    groups: C.Groups
    selected?: number
}
export default function Worlds( props: worlds_props){
    const [expanded, setExpanded]= useState(false)
    const {setWorld, name, groups, selected} = props

    const getWorlds = ()=>{
        return Object.entries(groups).map(group=>{
            let s = false
            if(selected ===parseInt(group[0]))
              s = true

            return <Button key={group[0]} onClick={(e)=>setWorld(group[0])} variant={s?'outlined':'text'}>{group[1].name}</Button>
        })
    }
    return (
        <Accordion key={'world'+name} expanded={expanded} onChange={()=>setExpanded(!expanded)}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography>{name}</Typography>
          
        </AccordionSummary>
        <AccordionDetails>
          {getWorlds()}
        </AccordionDetails>
      </Accordion>
    )
}