import { Button, Paper, ButtonGroup, FormControl, InputLabel, MenuItem, Select } from "@material-ui/core";
import { makeStyles } from '@material-ui/core/styles'
import {CSSProperties, useState} from 'react'
import { ControlKeys, ControlCommands} from "../util/constants";
import BlendDialog from "./BlendDialog";
import MenuDialog from './MenuDialog'
import Alert from './Alert'

interface command_props{
    handleCommand: (e: ControlKeys) => void
}
const useStyles = makeStyles((theme) => ({
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
  }));
export default function CommandPad(props:command_props){
    const {handleCommand } = props
    const [menuOpen, setMenuOpen] = useState(false)
    const [blendOpen, setBlendOpen] = useState(false)
    const [openPowerOnAlert, setOpenPowerOnAlert]= useState(false)
    const [openPowerOffAlert, setOpenPowerOffAlert]= useState(false)
    const classes = useStyles();
    const s: CSSProperties={

        width: '150px',
        height: '50px'

    }
    const handleSelectors=(e: any)=>{
        let target = e.target.value as ControlKeys
        handleCommand(target)

    }
    return(
       <>
        <Paper  key='commandKeys' style={{width:'300px'}}>
            <ButtonGroup key='power' color="primary" variant="outlined">
            <Button  key='shutter_on' style={s} onClick={(e) => handleCommand(ControlCommands.SHUTTER_CLOSED)} >Sutter Closed (i)</Button>
            <Button  key='sutter_off' style={s}  onClick={(e) => handleCommand(ControlCommands.SHUTTER_OPEN)} >Shutter Open (o)</Button>
            </ButtonGroup>
            <br></br>
            <ButtonGroup key='shutters' color="primary" variant="outlined">
                <Button  key='power off'  style={s}  onClick={(e) => setOpenPowerOffAlert(true)} >Power Off</Button>
                <Button  key='power on' style={s}  onClick={(e) => setOpenPowerOnAlert(true)} >Power On</Button>
            </ButtonGroup>
            <ButtonGroup color="primary" variant="outlined">
                <Button color="primary" key='menu' variant="contained" style={s}  onClick={(e) => setMenuOpen(!menuOpen)} >Menu</Button>
                <Button color="primary" key='blend' variant="contained" style={s}  onClick={(e) => setBlendOpen(!blendOpen)} >Blend</Button>
            </ButtonGroup>
            <FormControl className={classes.formControl}>
                <InputLabel id='test_pattren'>Test Pattren</InputLabel>
                <Select labelId='test_pattren' id='test_pattren_select'
                 value={''} onChange={((e)=> handleSelectors(e))}>
                     <MenuItem value={ControlCommands.TEST_PATTERN_OFF}>Off</MenuItem>
                     <MenuItem value={ControlCommands.TEST_PATTERN_WHITE}>White</MenuItem>
                     <MenuItem value={ControlCommands.TEST_PATTERN_BLACK}>Black</MenuItem>
                     <MenuItem value={ControlCommands.TEST_PATTERN_FOCUS_RED}>Focus Red</MenuItem>
                 </Select>
                 
            </FormControl>
            <MenuDialog open={menuOpen} handleClose={()=>setMenuOpen(false)} handleCommand={handleCommand}/>
            <BlendDialog open={blendOpen} handleClose={()=>setBlendOpen(false)} handleCommand={handleCommand}/>
        </Paper>
        <Alert open={openPowerOffAlert} handleCommand={handleCommand} handleClose={()=>setOpenPowerOffAlert(false)}
        header='Power Off' message='Are you sure?' command={ControlCommands.POWER_OFF}/>
        <Alert open={openPowerOnAlert} handleCommand={handleCommand} handleClose={()=>setOpenPowerOnAlert(false)}
        header='Power On' message='Are you sure?' command={ControlCommands.POWER_ON}/>
        </>
    )
}
