import * as C from './constants'

type cmdType = number | keyof typeof C.commands
export function processCMD(cmdLine: string, selected: number[], world: number[]) {
    let cmds = cmdLine.split(' ')
    let cmdArray: cmdType[] = []
   // console.log('Processing ', cmds)
    for (let i = 0; i < cmds.length; i++) {
      //  console.log('Now Running', cmds[i])
        if (cmds[i] === C.commands.THRU) {
            cmdArray.push(C.commands.THRU)
        } else {
            
            getCommandArray(cmds[i]).forEach(cmd => {
                cmdArray.push(cmd)
            })
            
           //cmdArray.concat(getCommandArray(cmds[i]))
        }
    }
   // console.log('Parsing Complete', cmdArray)
    selected = handleSelected( handleThru(cmdArray),selected)
    selected = selected.filter(s=> world.includes(s))
    
 //  console.log('Processing Complete', selected)
    return selected
}

function handleThru(cmds: cmdType[]) {
    let cmdArray: number[] = []
    if (cmds.indexOf(C.commands.THRU) === 1 && cmds.length > 2) {
        let negative = 1
        let start = cmds[0] as number
        if (start < 0) {
            start = start * -1
            negative = -1
        }
        let end = cmds[2] as number
        if(end<start){
          //  console.log('Running Thru Backwards',end, 'thru',start)
            for (let i = start; i >= end; i--) {
                console.log(i*negative)
                cmdArray.push(i*negative)
        }
        } else{
            for (let i = start; i <= end; i++) {
                cmdArray.push(i*negative)
        }
        }
        
        if(cmds.length>2)
            cmdArray= cmdArray.concat(handleThru(cmds.slice(3)))
    } else{
        return cmds.map(c=> {return parseInt(c.toString())})
    }
    return cmdArray
}

function handleSelected(cmds: number[], selected: number[]) {
    let newSelected = selected
    for (let i = 0; i < cmds.length; i++) {
        let cmd = cmds[i]
        if (cmd < 0 && newSelected.includes(cmd * -1)) { //Remove
            //console.log('Removing ',s[i]*-1)
            newSelected = newSelected.filter((n) => n !== (cmd * -1))
        } else if (!newSelected.includes(cmd) && cmd > 0) {
            newSelected.push(cmd)
        }
    }
   
    return newSelected
}

function getCommandArray(cmd: string) {
    if (cmd.length === 0) {
        console.groupEnd()
        return []
    }
   // console.group('Processing ', cmd)

    let array: number[] = []
    if (cmd.slice(0, 5) === C.commands.THRU) {
   //     console.log('Thru')
        //return [C.commands.THRU]
    } else {
        let num = findInt(cmd)
        array.push(num)
        // console.table(array)
      //  console.log(num, cmd.indexOf(num + ''))
        let reduced = cmd.slice(cmd.indexOf(num + "") + ((num + '').length))
        //console.log('Reduced',reduced)

        let reset = getCommandArray(reduced)
        for (let i = 0; i < reset.length; i++) {
            array.push(reset[i])
        }

        //console.table(array)

    }
    //array.push(getCommandArray(c.))
    return array
}
function findInt(cmd: string) {
    // console.log('Finding int', c)
    let i = 0
    let negative = 1
    if (cmd[0] === '-') {
        negative = -1
        cmd = cmd.slice(1)
    }

    // console.log('C ',c)
    while (i < cmd.length) {
        if (parseInt(cmd[i]) >= 0) {
            i++
        }
        else {
            i = cmd.length
        }
    }
    // console.log('Parsed Int' ,parseInt(c.slice(0,i))*negative)
    return (parseInt(cmd.slice(0, i)) * negative)
}
