import PJ from "./PJ"
import * as C from '../util/constants'
import { Paper, makeStyles, Table, TableBody, TableCell, TableContainer, TableHead, TableRow} from '@material-ui/core'
import { prefres } from "../prefrences"
import { CSSProperties } from "react"
interface Programmer_Props{
    selected: number[],
    active: number[],
    world: number[],
    prefs: prefres,
    status: C.RIG_STATUS,
    pjs: Record<number, C.PJ_OBJ>,
    handleTableSelect: (id:number)=> void
}
const useStyles = makeStyles({
    table:{
        minWidth:300,
        maxHeight:400
    }
})
export default function Programmer(props : Programmer_Props){
    const {active, world, selected, prefs, pjs, status, handleTableSelect} = props
    const classes = useStyles()
    const getPJ=(pjID:number):C.PJ_OBJ=>{
        let pj= pjs[pjID]
        if(!pj){
            return{
                id: pjID,
                online: 'false',
                shutter: 'Unknown',
                power: 'Unknown',
                lastSeen: -1,
                error: 'NOT SEEN',
                lampStatus: 'Unknown',
                edgeBlending: 'Unknown',
                edgeBlendingMarker: 'Unknown',
                testPattren: 'Unknown',
                edgeBlendingLeft: 'Unknown',
                edgeBlendingLower: 'Unknown',
                edgeBlendingRight: 'Unknown',
                edgeBlendingUpper: 'Unknown',
                hdmiResolution: 'Unknown',
                hmdiSignalLevel: 'Unknown',
                osdPostion: 'Unknown',
                inputSignalName_Main: 'Unknown',
                name: 'Unknown'
            }
        }
        return pj
    }
    const getPJs=()=>{
        world.sort()
       
        return world.map(s=>{
            let pj =getPJ(s)
            if((prefs.showOnlySelected&& selected.includes(s)) || !prefs.showOnlySelected){
                return <PJ key={'pj id_'+pj.id} pj={pj} handleTableSelect={handleTableSelect} selected={selected.includes(s)} darkMode={prefs.darkMode} active={active.includes(s)} />
            } 
            return <></>
            
        })
    }
    const getStyle=(good = true)=>{
        let s : CSSProperties={
            width: '50',
            textAlign: 'center'
        }
        if(!good)
         s.backgroundColor = prefs.darkMode? 'red': 'red'
        
        return s
    }
    return(
        <TableContainer  key='p-tc' component={Paper}>
            <Table key='p-t' className={classes.table}>
                <TableHead key='p-th'>
                    <TableRow key='p-r'>
                        <TableCell style={getStyle()} key='p_pj'> PJ </TableCell>
                        <TableCell style={getStyle(status.shutter)} key='p_s'> Shutter</TableCell>
                        <TableCell style={getStyle(status.blend)} key='p_b'> Blend</TableCell>
                        <TableCell style={getStyle(status.blendMarker)} key='p_bm'> Blend Marker</TableCell>
                        <TableCell style={getStyle(status.testPattren)} key='p_test'> Test Pattern</TableCell>
                        <TableCell style={getStyle(status.lampStatus)} key='p_lamp'> Lamp Status</TableCell>
                        <TableCell style={getStyle(status.hdmiInput)} key='p_hdmi'> HDMI Input</TableCell>
                        <TableCell style={getStyle(status.signalName)} key='p_signal name'> Signal Name</TableCell>
                        <TableCell style={getStyle()}  key='p_ls'> Last Seen</TableCell>

                    </TableRow>
                </TableHead>
                <TableBody>
                    {getPJs()}
                </TableBody>
            </Table>
        </TableContainer>
       
    )
}

