import { TableCell, TableRow, Button } from "@material-ui/core"
import BorderColorIcon from '@material-ui/icons/BorderColor';
import { CSSProperties, useEffect, useState } from "react"
import { PJ_OBJ } from "../util/constants"
interface PJ_Props {
    pj: PJ_OBJ,
    selected: boolean,
    active: boolean,
    darkMode: boolean,
    handleTableSelect: (id: number) => void
}
function PJ(props: PJ_Props) {
    const { pj, selected, active, darkMode, handleTableSelect } = props
    const [time, setTime] = useState('0')

    useEffect(() => {
        
      //  getTime()
        
        let i =setInterval(getTime,1000)
        
        return function cleanup(){
            clearInterval(i)
        }
       
        
    })

    const style = () => {
     //   console.log('Selected',selected, 'Active',active)
        let s: CSSProperties = {
            color: 'Gainsboro',
            textAlign: 'center'

        }
        if (active) {
            s.color = 'black'
            s.background = 'lightgray'

        } else if (selected) {
            s.color = 'black'
            
        }
        return s
    }
    const bStyle = (b = 'On', goodWord = 'On'):CSSProperties => {
        return  {
            textAlign: 'center',
            margin:'auto',
            color: b===goodWord? darkMode?'white' :'black' : 'red'
            
        }
        
    }
    const getTime = () => {
        if (pj.lastSeen < 0) {
            setTime('N/A')
            return 
        }
        let t = (Date.now() - pj.lastSeen) / 1000

        t = Math.floor(t)
        if (t > 59) {
            setTime(Math.floor(t / 60) + 'm')
            return
        }
            setTime(t + 's')
        
    }

    const blendRegion =()=>{
        let color = ''
        let regions = [pj.edgeBlendingUpper, pj.edgeBlendingRight, pj.edgeBlendingLower, pj.edgeBlendingLeft]
        for(let i=0; i < regions.length; i++){
            switch(regions[i]){
                case 'On':
                    color+= ' green'
                    break;
                case 'Off':
                    color+= ' black'
                    break
                case 'Unknown':
                    color+= ' yellow'
                    break
                default:
                    color+=' red'
            }
        }
      
        return (
            <p style={{ padding: 10,  margin:'auto', textAlign: 'center', borderStyle: 'solid', borderColor: color}}key={pj.id+'brbox'}> {pj.edgeBlending}
             <BorderColorIcon  style={{fontSize:'15', marginLeft: '5', marginTop: 'auto', color:pj.edgeBlendingMarker==='On'?'red':'gray'}} /></p>
        )
    }
    const getID=()=>{
        return <Button> {pj.name==='Unknown'?pj.id:pj.name}</Button>
    }

    return (
        <TableRow key={pj.id}>
            <TableCell onClick={e => handleTableSelect(pj.id)} style={style()} key={pj.id + 'id'}>{getID()}</TableCell>
            <TableCell style={bStyle(pj.shutter, 'Open')} key={pj.id + 'shutter'}>{pj.shutter}</TableCell>
            <TableCell style={bStyle(pj.edgeBlending, 'On')} key={pj.id+"blend"}> {blendRegion()} </TableCell>
            <TableCell style={bStyle(pj.edgeBlendingMarker, 'Off')}key={pj.id+'blendmarker'}>{pj.edgeBlendingMarker}</TableCell>
            <TableCell style={bStyle(pj.testPattren, 'Off')}key={pj.id+'test'}>{pj.testPattren}</TableCell>
            <TableCell style={bStyle(pj.lampStatus, 'Lamp On')} key={pj.id+'lamp'}>{pj.lampStatus}</TableCell>
            <TableCell style={bStyle(pj.hdmiResolution,'1920x1200p')} key={pj.id+'hdmi'}>{pj.hdmiResolution}/{pj.hmdiSignalLevel}</TableCell>
            <TableCell style={bStyle(pj.inputSignalName_Main,'1920x1200/60RB')} key={pj.id+'signal_name_main'}>{pj.inputSignalName_Main}</TableCell>
            <TableCell style={bStyle()} key={pj.id + 'last'}>{time}</TableCell>
        </TableRow>

    )
}

export default PJ

