import { Dialog, AppBar, Toolbar, Typography, IconButton, Button, Paper, Slide, Switch, TextField, FormControlLabel, makeStyles } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import { useState, forwardRef, useEffect } from "react";
import * as C from "../util/constants";
import { setConfig } from "../util/getter";
import Patch from "./Patch";
import socketIOClinet from 'socket.io-client'
import { SocketPatcher } from "../util/SocketListener";
const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '25ch',
    },
  },
}));

const Transition = forwardRef(function Transition(props, ref) {
  //@ts-ignore
  return (<Slide direction="up" ref={ref} {...props} />);
});

interface ConfigDialog_Props {
  open: boolean
  handleClose: () => void
  config: C.Config
}
export default function ConfigDialog(props: ConfigDialog_Props) {
  const { open, handleClose, config } = props
  const [polling, setPolling] = useState(config.Polling)
  const [pollingInterval, setPollingInterval] = useState(config.Polling_Interval/1000)
  const classes = useStyles();
  const [patch, setPatch] = useState<C.Patch>(config.Patch)
  useEffect(() => {
    setPollingInterval(config.Polling_Interval/1000)
    setPolling(config.Polling)
    setPatch(config.Patch)
  }, [config])
 

  const handleSubmit = (event: any) => {
    event?.preventDefault();
    // console.log(event)

    let form = document.querySelector('form')
    if (form) {
      const data = new FormData(form);
      let query = ''
      for (var pair of data.entries()) {
      
        let t = pair[0] as C.ConfigHeaders
       
        let v = pair[1].toString()
        switch(t){
          case C.ConfigHeaders.POLLING:
           
            break;
          case C.ConfigHeaders.POLLING_INTERVAL:
            query += t + '=' + pollingInterval*1000 + '&'
            break;
          default:
            query += t + '=' + v + '&'
            break;
        }

      }
      //Hard adds Polling to ensure it is accounted for
      query += C.ConfigHeaders.POLLING + '=' + polling
      
      setConfig(query)
      SocketPatcher(socketIOClinet(), patch)
    }
  }
  const handlePollingInterval=(value: number)=>{
    if(value>10){
      setPollingInterval(value)
    }
  }
  const handlePatchUpdate=(p: C.Patch)=>{
    console.log(p)
    setPatch(p)
  }
  return (
    //@ts-ignore
    <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>

      <form className={classes.root} onSubmit={handleSubmit}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Server Configuration
            </Typography>
            <Button autoFocus color="inherit" type={'submit'} onClick={handleClose}>
              Save
            </Button>
          </Toolbar>
        </AppBar>

        <Paper key='config_paper'>



          <TextField key='location' style={{width:'25%'}}label={'Location'} name={C.ConfigHeaders.LOCATION} defaultValue={config.Location} />

          <FormControlLabel name={C.ConfigHeaders.POLLING} value={polling} labelPlacement="top"
            control={<Switch checked={polling} value={polling} onChange={(e) => setPolling(!polling)} name={C.ConfigHeaders.POLLING} />}
            label="Polling"
          />

          <TextField key='polling_interval' style={{width:'25%'}} disabled={!polling} type='number' label={'Poll Interval'} onChange={(e)=> handlePollingInterval(parseInt(e.target.value))} name={C.ConfigHeaders.POLLING_INTERVAL} value={pollingInterval} />
        </Paper>


        <Paper key='patch'>
          <Patch patch={patch} setPatch={handlePatchUpdate} />
        </Paper>
      </form>
      <span style={{ fontSize: 10 }}>{config.LastUpdated}</span>
    </Dialog>

  )
}